import { Clip, createFFmpeg } from '@webav/av-cliper'

// URL <sup data-citation='{&quot;id&quot;:3,&quot;url&quot;:&quot;&lt;!DOCTYPE html&gt; &lt;html la...Root)}}})}})()&lt;/script&gt;&quot;,&quot;title&quot;:&quot;&lt;!DOCTYPE html&gt; &lt;html la...Root)}}})}})()&lt;/script&gt;&quot;,&quot;content&quot;:&quot;   Notes    AList V3         Docker  PaaS           FAQ OpenList   See also  API  admin user meta driv&quot;}'>3</sup><sup data-citation='{&quot;id&quot;:5,&quot;url&quot;:&quot;&lt;!DOCTYPE html&gt; &lt;html la...Root)}}})}})()&lt;/script&gt;&quot;,&quot;title&quot;:&quot;&lt;!DOCTYPE html&gt; &lt;html la...Root)}}})}})()&lt;/script&gt;&quot;,&quot;content&quot;:&quot;IFRAME PREVIEWS IFRAME    iframe  HTML    OnlyOffice  Office  json doc,docx,xls,xlsx,ppt,pptx: OnlyOffi&quot;}'>5</sup>
const urlParams = new URLSearchParams(window.location.search)
const videoUrl = urlParams.get('url') ? atob(urlParams.get('url')) : 'demo.mp4'

// DOM
const sourceVideo = document.getElementById('sourceVideo')
const previewVideo = document.getElementById('previewVideo')
const cutBtn = document.getElementById('cutBtn')
const exportBtn = document.getElementById('exportBtn')
const timelineMarkers = document.getElementById('timelineMarkers')

let cliper = null
let trimStart = 0
let trimEnd = 0

// 
sourceVideo.src = videoUrl
sourceVideo.onloadeddata = async () => {
  cliper = new Clip(sourceVideo) // <sup data-citation='{&quot;id&quot;:1,&quot;url&quot;:&quot;&lt;!DOCTYPE html&gt; &lt;html da...lass=videoAiLogo&gt;&lt;/div&gt;&quot;,&quot;title&quot;:&quot;&lt;!DOCTYPE html&gt; &lt;html da...lass=videoAiLogo&gt;&lt;/div&gt;&quot;,&quot;content&quot;:&quot;K EN OF CONTENTS     v1  install webavav-cliper npm install webavav-canvas  webav   - doc-site  - docs md - packages pnpm  monorepo&quot;}'>1</sup><sup data-citation='{&quot;id&quot;:4,&quot;url&quot;:&quot;&lt;!DOCTYPE html&gt; &lt;html da...lass=videoAiLogo&gt;&lt;/div&gt;&quot;,&quot;title&quot;:&quot;&lt;!DOCTYPE html&gt; &lt;html da...lass=videoAiLogo&gt;&lt;/div&gt;&quot;,&quot;content&quot;:&quot;aWxsPSIjMmMzZTUwIi8+CiAgICAgICAgICAgIDwvZz4KICAgICAgICAgIDwvZz4KICAgICAgICA8L2c+CiAgICAgIDwvZz4KICAgICAgPGcgaWQ9Ikdyb3VwXzE2MCI+CiAgICAgICAgPGcgaWQ9IkdyYXBoaWNfMTYzIj4KICAgICAgICAgIDxwYXRoIGQ9Ik0gODA1&quot;}'>4</sup>
  
  // 
  createTimeline(sourceVideo.duration)
}

// 
function createTimeline(duration) {
  timelineMarkers.innerHTML = ''
  for (let i = 0; i < duration; i += 3) {
    const marker = document.createElement('div')
    marker.className = 'timeline-marker'
    marker.textContent = `${i}s`
    marker.style.left = `${(i/duration)*100}%`
    timeline.addEventListener('click', () => handleMarkerClick(i))
    timelineMarkers.appendChild(marker)
  }
}

// 
cutBtn.addEventListener('click', async () => {
  if (!cliper) return
  
  // UI
  await cliper.setRange(trimStart, trimEnd)
  
  // 
  const resultStream = await cliper.cutToMediaStream()
  previewVideo.srcObject = resultStream
})

// 
exportBtn.addEventListener('click', async () => {
  if (!cliper) return
  
  // 
  const output = await cliper.exportToFile('edited.mp4')
  
  // 
  const downloadLink = document.createElement('a')
  downloadLink.href = URL.createObjectURL(output)
  downloadLink.download = '.mp4'
  downloadLink.click()
})
