interface IClipMeta {
  width: number;
  height: number;
  duration: number;
}

/**
 * 
 *
 * Clip
 *
 * WebAV  {@link MP4Clip}, {@link AudioClip}, {@link ImgClip}, {@link MediaStreamClip}  {@link Combinator} {@link AVCanvas} 
 *
 * 
 * @see [](https://webav-tech.github.io/WebAV/demo/2_6-custom-clip)
 *
 */
export interface IClip {
  /**
   * 
   * @param time  
   */
  tick: (time: number) => Promise<{
    video?: VideoFrame | ImageBitmap | null;
    audio?: Float32Array[];
    state: 'done' | 'success';
  }>;

  /**
   * ready  resolved 
   */
  readonly ready: Promise<IClipMeta>;

  /**
   * 
   */
  readonly meta: IClipMeta;

  /**
   * clone
   */
  clone: () => Promise<this>;

  /**
   * 
   *
   * 
   *
   * @param time 
   * @returns
   */
  split?: (time: number) => Promise<[this, this]>;

  /**
   * 
   */
  destroy: () => void;
}

/**
 *   
 */
export const DEFAULT_AUDIO_CONF = {
  sampleRate: 48000,
  channelCount: 2,
  codec: 'mp4a.40.2',
} as const;
